.header {
  padding: 30px 0;
  display: flex;
  align-items: flex-start;

  @include vp-1199 {
    padding: 34px 0 17px 0;
  }

  @include vp-767 {
    padding: 16px 0;
  }
}

.header__logo-link {
  margin-right: auto;
  margin-top: 10px;

  @include vp-1199 {
    margin-top: 6px;
  }

  @include vp-767 {
    margin-top: 0;
  }
}

.header__logo-icon {
  @include vp-1199 {
    width: 82px;
    height: 17px;
  }

  @include vp-767 {
    width: 69px;
    height: 24px;
  }
}

.header__link {
  font-family: $ff-secondary;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: $color-default-white;

  @include vp-1199 {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 24px;
  }
}
